@mixin font-sizes(){
  .foobar {
	line-height: 1.857;
	font-size: 14px;

	&.fbr-font-14px {
	  line-height: 1.857;
	  font-size: 14px;
	}
	&.fbr-font-16px {
	  line-height: 1.813;
	  font-size: 16px;
	}
	&.fbr-font-18px {
	  line-height: 1.778;
	  font-size: 18px;
	}
	&.fbr-font-20px {
	  line-height: 1.7;
	  font-size: 20px;
	}
	&.fbr-font-22px {
	  line-height: 1.682;
	  font-size: 22px;
	}
	&.fbr-font-24px {
	  line-height: 1.667;
	  font-size: 24px;
	}
  }

  @media only screen and (max-width: $screen-md){
	.foobar {
	  line-height: 1.625;
	  font-size: 16px;

	  &.fbr-font-14px {
		line-height: 1.625;
		font-size: 16px;
	  }
	  &.fbr-font-16px {
		line-height: 1.611;
		font-size: 18px;
	  }
	  &.fbr-font-18px {
		line-height: 1.6;
		font-size: 20px;
	  }
	  &.fbr-font-20px {
		line-height: 1.591;
		font-size: 22px;
	  }
	  &.fbr-font-22px {
		line-height: 1.583;
		font-size: 24px;
	  }
	  &.fbr-font-24px {
		line-height: 1.577;
		font-size: 26px;
	  }
	}
  }
}
