.fbr-toggle-circle {
  &.fbr-closed {
	.fbr-toggle {
	  border-radius: 50%;
	}
	&.fbr-layout-top,
	&.fbr-layout-top-left,
	&.fbr-layout-top-right {
	  .fbr-toggle {
		top: $toggle-offset;
	  }
	}
	&.fbr-layout-bottom,
	&.fbr-layout-bottom-left,
	&.fbr-layout-bottom-right {
	  .fbr-toggle {
		bottom: $toggle-offset;
	  }
	}
	&.fbr-layout-right,
	&.fbr-layout-right-top,
	&.fbr-layout-right-bottom {
	  .fbr-toggle {
		right: $toggle-offset;
	  }
	  &.fbr-toggle-right {
		.fbr-toggle {
		  right: $toggle-offset;
		}
	  }
	  &.fbr-toggle-left {
		.fbr-toggle {
		  left: calc(100% - #{$toggle-size + $toggle-offset});
		}
	  }
	}
	&.fbr-layout-left,
	&.fbr-layout-left-top,
	&.fbr-layout-left-bottom {
	  .fbr-toggle {
		left: $toggle-offset;
	  }
	  &.fbr-toggle-right {
		.fbr-toggle {
		  right: calc(100% - #{$toggle-size + $toggle-offset});
		}
	  }
	  &.fbr-toggle-left {
		.fbr-toggle {
		  left: $toggle-offset;
		}
	  }
	}
  }
  &.fbr-open {
	.fbr-toggle {
	  border-radius: 0;
	}
  }
}
