.fbr-prev,
.fbr-next {
  @include button-reset($display: none);
  width: 46px;
  height: 100%;
  min-width: 46px;
  min-height: 46px;
  position: relative;
  z-index: 2;
  transition-property: visibility, opacity, background-color, color;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}
.fbr-show-nav {
  .fbr-prev,
  .fbr-next {
	display: inline-flex;
  }
}

@media (hover: hover) {
  .fbr-hover-buttons {
	.fbr-prev,
	.fbr-next {
	  position: absolute;
	  top: 0;
	  bottom: 0;
	  visibility: hidden;
	  opacity: 0;
	}
	.fbr-prev {
	  left: 0;
	}
	.fbr-next {
	  right: 0;
	}
	.fbr-content:hover {
	  .fbr-prev,
	  .fbr-next {
		visibility: visible;
		opacity: 1;
	  }
	}
  }
}
