.fbr-icon {
  display: inline-block;
  fill: currentColor;
  width: $icon-size;
  height: $icon-size;
}

@keyframes foobar-rotating {
  from { transform: rotate(0); }
  to { transform: rotate(360deg); }
}

.fbr-icon-spinner,
.fbr-icon-spinner2,
.fbr-icon-spinner3,
.fbr-icon-spinner4,
.fbr-icon-spinner5,
.fbr-icon-spinner6 {
  animation: foobar-rotating 2s linear infinite;
}
