@mixin button-reset(
  $font-size: inherit,
  $font-weight: inherit,
  $font-family: inherit,
  $text-decoration: none,
  $border: none,
  $outline: none,
  $box-shadow: none,
  $padding: 0,
  $margin: 0,
  $vertical-align: middle,
  $display: inline-flex,
  $center-flex: true,
  $cursor: pointer
) {

  display: $display;
  @if $center-flex {
	align-items: center;
	align-content: center;
	justify-content: center;
  }
  cursor: $cursor;
  padding: $padding;
  margin: $margin;
  color: inherit;
  background-color: inherit;
  font-size: $font-size;
  font-family: $font-family;
  font-weight: $font-weight;
  text-decoration: $text-decoration;
  vertical-align: $vertical-align;
  border: $border;
  outline: $outline;
  box-shadow: $box-shadow;
  pointer-events: auto;

  &:focus,
  &:active,
  &:hover {
	border: $border;
	outline: $outline;
	box-shadow: $box-shadow;
	text-decoration: $text-decoration;
  }
}
