/* Base Toggle Styles */
.fbr-toggle {
  @include button-reset;

  position: absolute;
  width: $toggle-size;
  height: $toggle-size;
  min-width: $toggle-size;
  min-height: $toggle-size;
  z-index: 3;
  visibility: visible;
  opacity: 1;
}

.fbr-toggle-none {
  .fbr-toggle {
	display: none;
  }
  .fbr-content {
	padding-left: 0!important;
	padding-right: 0!important;
  }
}

/* Toggle Right Styles */
.fbr-toggle-right {
  .fbr-toggle {
	right: 0;
  }
  .fbr-content {
	padding-right: $toggle-size;
  }
}
/* Toggle Left Styles */
.fbr-toggle-left {
  .fbr-toggle {
	left: 0;
  }
  .fbr-content {
	padding-left: $toggle-size;
  }
}

/* Toggle Open Styles */
.fbr-open {
  .fbr-toggle {
	height: 100%;
	.fbr-expand-icon {
	  display: none;
	}
  }
}

/* Toggle Closed Styles */
.fbr-closed {
  .fbr-toggle {
	height: $toggle-size;
	.fbr-dismiss-icon,
	.fbr-collapse-icon {
	  display: none;
	}
  }
}

/* Toggle Dismissed Styles */
.fbr-dismissed {
  .fbr-toggle {
	visibility: hidden;
	opacity: 0;

	.fbr-expand-icon {
	  display: none;
	}
	.fbr-dismiss-icon,
	.fbr-collapse-icon {
	  display: inline-block;
	}
  }
}
