.fbr-item {
  .fbr-message {
	a {
	  margin: 0 .25em;
	}
  }

  .fbr-input {
	font: inherit;
	line-height: inherit;
	height: 2.5em;
	max-width: 100%;
	width: 12em;
	min-width: 6em;
	box-shadow: 0 0 0 transparent;
	border-radius: 4px;
	border: none;
	background-color: #ffffff;
	color: #32373c;
	padding: $m-0 $m-sm;
	margin: 0;

	&:focus {
	  border: none;
	  box-shadow: 0 0 0 1px #007cba;
	  outline: 2px solid transparent;
	}

	&.fbr-invalid {
	  border: none;
	  box-shadow: 0 0 0 1px #dc3232;
	  outline: 2px solid transparent;
	}

	&.fbr-shake {
	  animation: foobar-invalid-shake 0.2s ease-in-out;
	}

	&::placeholder {
	  color: #72777c;
	}
  }

  .fbr-button {
	@include button-reset($font-size: inherit, $padding: $m-0 $m-md, $margin: 0);
	position: relative;
	line-height: inherit;
	height: 2.5em;
	width: auto;
	//flex-shrink: 0;
	flex-grow: 1;
	border-radius: 4px;
	white-space: nowrap;
	transition-property: background-color, color, border-color, box-shadow, transform;
	transition-timing-function: ease-in-out;
	transition-duration: 0.2s;

	&:disabled {
	  cursor: not-allowed;
	  pointer-events: none;
	}
  }

  .fbr-input-with-button {
	position: relative;
	display: flex;
	width: 100%;
	justify-content: center;
	align-content: center;
	align-items: center;
	flex-wrap: nowrap;
	flex-direction: row;
	border-radius: 4px;

	&:before,
	&:after {
	  transform-origin: 12em center;
	  background: linear-gradient(to right, #FFFFFF, #FFFFFF 12em, #555555 12em, #555555);
	}

	.fbr-input {
	  border-radius: 4px 0 0 4px;
	}
	.fbr-button {
	  border-radius: 0 4px 4px 0;
	  z-index: 1;
	}
  }
}

.fbr-rtl {
  .fbr-input-with-button {
	.fbr-input {
	  border-radius: 0 4px 4px 0;
	}
	.fbr-button {
	  border-radius: 4px 0 0 4px;
	}
  }
}

@keyframes foobar-invalid-shake {
  0% { transform: translateX(0); }
  25% { transform: translateX(0.25em); }
  75% { transform: translateX(-0.25em); }
  100% { transform: translateX(0); }
}

// medium size switch @ 782px
@mixin tablet-item-ui(){
  .fbr-item {
	.fbr-input,
	.fbr-button {
	  min-height: 40px;
	}
	.fbr-tablet-100 {
	  width: calc(100% - #{$m-0 * 2});
	}
	.fbr-tablet-50 {
	  width: calc(50% - #{$m-0 * 2});
	}
	.fbr-tablet-33 {
	  width: calc(33.3% - #{$m-0 * 2});
	}
  }
}

// small size switch @ 480px
@mixin mobile-item-ui(){
  .fbr-item {
	.fbr-mobile-100 {
	  width: calc(100% - #{$m-0 * 2});
	}
	.fbr-mobile-50 {
	  width: calc(50% - #{$m-0 * 2});
	}
	.fbr-mobile-33 {
	  width: calc(33.3% - #{$m-0 * 2});
	}
  }
}
