.foobar {
  position: relative;
  display: none;
  width: auto;
  height: auto;
  min-height: $toggle-size;
  max-width: 100%;
  max-height: 100%;
  margin: 0;
  padding: 0;
  box-shadow: none;
  border: none;
  font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
  box-sizing: border-box;
  z-index: 99900;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;

  &.fbr-initialized {
	visibility: visible;
	opacity: 1;
  }

  &.fbr-rtl {
	direction: rtl;
  }
}

@include font-sizes();
